:root{
  --primaryColor:#181c24;
  --backColor: #50678a;
  --backColor2: #FFA726;
}

.App {
  background-color: var(--primaryColor);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(18px);
  color: white;
}

.top-shadow{
  position: relative;
  width: 100%;
  height:15px;
  background:linear-gradient(rgba(53, 53, 53, 0.5),transparent);
}

section{
  min-height: 100vh;
}

button{
  cursor:pointer;
  border:none;
  max-width: 100%;
  outline:none;
  
}


*{
  box-sizing: border-box;
  padding:0px;
  margin:0px;

}